import React, { useState } from "react";

function CompoundingCalc() {
  function CI() {
    var index = document.getElementById("mySelect").selectedIndex;

    var amount, rate, years, principal;
    
    if (index === 0) {
      rate = parseFloat(document.getElementById("rate").value) / 100;
      years = parseFloat(document.getElementById("years").value);
      principal = parseFloat(document.getElementById("principal").value);

      amount = principal * (1 + rate) ** years;
      amount = amount.toFixed();
      document.getElementById("answer").innerHTML = amount;
      document.getElementById('answer-type').innerHTML = "Total Amount"
    }
    else if(index === 1){
      rate = parseFloat(document.getElementById("rate").value) / 100;
      years = parseFloat(document.getElementById("years").value);
      amount = parseFloat(document.getElementById("amount").value);
      
      principal = amount / ((1 + rate) ** years);
      principal = principal.toFixed(2);
      document.getElementById("answer").innerHTML = principal;
      document.getElementById('answer-type').innerHTML = "Principal"
    }
    else if(index === 2){
      years = parseFloat(document.getElementById("years").value);
      principal = parseFloat(document.getElementById("principal").value);
      amount = parseFloat(document.getElementById("amount").value);

      rate = (((amount / principal) ** (1/years)) - 1) * 100;
      rate = rate.toFixed(4)
      document.getElementById("answer").innerHTML = rate;
      document.getElementById('answer-type').innerHTML = "Rate (in %)"
    }
    else if(index === 3){
      rate = parseFloat(document.getElementById("rate").value) / 100;
      principal = parseFloat(document.getElementById("principal").value);
      amount = parseFloat(document.getElementById("amount").value);
      
      years = (Math.log(amount/principal)) / (Math.log(1+rate))  ; 
      document.getElementById("answer").innerHTML = years;
      document.getElementById('answer-type').innerHTML = "Years"
    }

    
  }



  function clearScreen(){
    document.getElementById("answer").innerHTML = "";
    document.getElementById('answer-type').innerHTML = ""
    document.getElementsByTagName("input").value = ''

  }

  const [active, setActive] = useState("Total Amount");

  function getEntity() {

    var index = document.getElementById("mySelect").selectedIndex;
    if (index === 0) {
      setActive("Total Amount");
    } else if (index === 1) {
      setActive("Principal");
    } else if (index === 2) {
      setActive("Rate");
    } else if (index === 3) {
      setActive("Years");
    }
  }

  return (
    <div className="container">
      
      <div id="display-screen">
        <p id="answer-type"></p>
        <h1 className="answer" id="answer"></h1>
      </div>
      <div id="form-screen">
        <form>
          <select
            onChange={getEntity}
            id="mySelect"
            className="btn btn-secondary dropdown-toggle"
          >
            <option className="dropdown-item" value="A">
              Total Amount (A)
            </option>
            <option className="dropdown-item" value="P">
              Principal (P)
            </option>
            <option className="dropdown-item" value="R">
              Rate of Intrest (R)
            </option>
            <option className="dropdown-item" value="T">
              Number of Years (T)
            </option>
          </select>
          <br />
          <div id="form">
            {active === "Total Amount" && (
              <div>
                <label>Principal</label>
                <input id="principal" type="text" />
                <br />
                <label>Rate of Intrest</label>
                <input id="rate" type="text" />
                <br />
                <label>Number of Years</label>
                <input id="years" type="number" />
                <br />
              </div>
            )}
            {active === "Principal" && (
              <div>
                <label>Amount</label>
                <input id="amount" type="text" />
                <br />
                <label>Rate of Intrest</label>
                <input id="rate" type="text" />
                <br />
                <label>Number of Years</label>
                <input id="years" type="number" />
                <br />
              </div>
            )}
            {active === "Rate" && (
              <div>
                <label>Amount</label>
                <input id="amount" type="text" />
                <br />
                <label>Principal</label>
                <input id="principal" type="text" />
                <br />
                <label>Number of Years</label>
                <input id="years" type="number" />
                <br />
              </div>
            )}
            {active === "Years" && (
              <div>
                <label>Amount</label>
                <input id="amount" type="text" />
                <br />
                <label>Principal</label>
                <input id="principal" type="text" />
                <br />
                <label>Rate</label>
                <input id="rate" type="number" />
                <br />
              </div>
            )}
          </div>
        </form>

        <button onClick={CI} className="btn btn-dark" type="submit">
          Calculate
        </button>
        <button onClick={clearScreen} className="btn btn-dark">Clear</button>
      </div>
    </div>
  );
}

export default CompoundingCalc;
